import React, {Component} from 'react';
import {render} from 'react-dom';
import {Table, Tag, Input, Row, Col, Button, Select, DatePicker, Breadcrumb} from 'antd';
import {ArrowUpOutlined, ArrowDownOutlined, PlusOutlined, MinusOutlined} from '@ant-design/icons';
import {Link} from "react-router-dom";
import {request} from './../../../utils/request';
import Top from '../../../component/top/index';
import Label from "../../../component/label/index";
import PolicyManagementMenu from "../../../component/policyManagementMenu/index";
import './index.css';

const {Search} = Input;
const {Option} = Select;
const {RangePicker} = DatePicker;

class PolicyList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            arrdown: true,
            arrProduct: false,
            // labelTheme:{
            //         title:"",
            //         item:["","","","","","","","","","",""]
            //     },
            // labelType:
            //     {
            //         title:"",
            //         item:["","","","","",""]
            //     },
            // labelProduct:{
            //     title:"",
            //     item:["","","","","","","","","","",""]
            // },
            labelStatus: {
                title: "",
                item: [
                    {
                        id: 0,
                        name: ""
                    },
                    {
                        id: 1,
                        name: ""
                    },
                    {
                        id: 2,
                        name: ""
                    }]
            },
            labelSource: {
                title: "",
                item: [ {
                    id: 0,
                    name: ""
                },
                    {
                        id: 1,
                        name: ""
                    },
                    {
                        id: 2,
                        name: ""
                    }]
            }
        }
        this.columns = [
            {
                title: '',
                dataIndex: 'title',
                key: 'title',
                width: 200,
                render: text => <a>{text}</a>,
            },
            {
                title: '',
                dataIndex: 'hierarchy',
                key: 'hierarchy'
            },
            {
                title: '',
                dataIndex: 'address',
                key: 'address',
            },
            {
                title: '',
                dataIndex: 'theme',
                key: 'theme'
            },
            {
                title: '',
                dataIndex: 'type',
                key: 'type'
            },
            {
                title: '',
                dataIndex: 'analysis',
                key: 'analysis'
            },
            {
                title: '',
                dataIndex: 'status',
                key: 'status'
            },
            {
                title: '',
                dataIndex: 'source',
                key: 'source'
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
                width: 130
            },
            {
                title: '',
                key: 'money',
                dataIndex: 'money'
            },
            {
                title: '',
                key: 'action',
                render: (text, record) => (<span><a></a><a className="ml15"></a></span>),
            },
        ];

        this.data = [
            {
                key: '1',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type: '',
                analysis: '-',
                status: '',
                source: '',
                time: '2019-02-01 12:05:11',
                money: ''
            },
            {
                key: '2',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type: '',
                analysis: '-',
                status: '',
                source: '',
                time: '2019-02-01 12:05:11',
                money: ''
            },
            {
                key: '3',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type: '',
                analysis: '-',
                status: '',
                source: '',
                time: '2019-02-01 12:05:11',
                money: ''
            },
            {
                key: '4',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type: '',
                analysis: '-',
                status: '',
                source: '',
                time: '2019-02-01 12:05:11',
                money: ''
            }
        ];

        function onShowSizeChange(current, pageSize) {
            console.log(current, pageSize);
        }

        this.pagination = {
            showSizeChanger: true,
            defaultCurrent: 1,
            total: 500,
            pageSizeOptions: ['10', '20', '30', '50', '100', '150'],
            onShowSizeChange: onShowSizeChange
        }
    }

    async componentWillMount() {
        const labelThemeData = await request('/common/get-all-policy-theme-label', 'POST'); //
        const labelTypeData = await request('/common/get-all-use-type-label', 'POST'); //
        const selectBelongData = await request('/common/get-all-belong-label', 'POST'); //
        const selectIndustryData = await request('/common/get-all-industry-label', 'POST'); //


        const themData = labelThemeData.data;
        const typeData = labelTypeData.data;
        const belongData = selectBelongData.data;
        const industryData = selectIndustryData.data;

        if (themData && themData.success && typeData && themData.success && belongData && belongData.success && industryData && industryData.success) {
            const allItem = {id: 0,name: ""};
            themData.data.unshift(allItem);
            typeData.data.unshift(allItem);
            belongData.data.unshift(allItem);
            industryData.data.unshift(allItem);
            this.setState({
                labelTheme: {
                    title: "",
                    item: themData.data
                },
                labelType: {
                    title: "",
                    item: typeData.data
                },
                belongData: belongData.data,
                industryData: industryData.data

            })
        }
    }

    setArrdown = () => {
        this.setState({
            arrdown: !this.state.arrdown
        })
    }
    setArrProduct = () => {
        this.setState({
            arrProduct: !this.state.arrProduct
        })
    }
    belongChange = async (value) => {
        const labelProductData = await request('/common/get-all-organization-label', 'POST', {belong_id: value}); //
        const productData = labelProductData.data;
        if (productData && productData.success) {
            this.setState({
                labelProduct: {
                    title: "",
                    item: productData.data
                }
            })
        }
    }

    render() {
        const {arrdown, labelTheme, labelType, labelProduct, arrProduct, labelStatus, labelSource, belongData, industryData} = this.state;
        return (
            <div className="policyList-template">
                <Top/>
                <div className="policyList-label-box max-weight-box">
                    <Row>
                        <Col span={4}>
                            <PolicyManagementMenu/>
                        </Col>
                        <Col span={20}>
                            <div className="information-title"></div>
                            <Breadcrumb separator=">">
                                <Breadcrumb.Item></Breadcrumb.Item>
                                <Breadcrumb.Item href=""></Breadcrumb.Item>
                            </Breadcrumb>
                            <div className="label-box">
                                <Row className="mt10">
                                    <Col span={2}></Col>
                                    <Col span={22}>
                                        <Input/>
                                    </Col>
                                </Row>
                                {labelTheme ?
                                    <Label title={labelTheme.title} item={labelTheme.item} key="labelTheme"/> : ''}
                                <Row className="mt10">
                                    <Col span={2}></Col>
                                    <Col span={22}>
                                        <Select style={{width: 300}} onChange={this.belongChange}>
                                            {belongData ? belongData.map((item, idx) => <Option value={item.id}
                                                                                                key={item.id}>{item.name}</Option>) : ''}
                                        </Select>
                                    </Col>
                                </Row>
                                <div className="label-product-box">
                                    {labelProduct ?
                                        <Label title={labelProduct.title} item={labelProduct.item} key="labelProduct"
                                               span={21} className={arrProduct ? "allLabel" : "minLabel"}/> : ''}
                                    {labelProduct ? (!arrProduct ? <span onClick={this.setArrProduct}
                                                                         className="more-label"><PlusOutlined/> </span> :
                                        <span onClick={this.setArrProduct}
                                              className="more-label"><MinusOutlined/> </span>) : ''}
                                </div>
                                {labelType ?
                                    <Label title={labelType.title} item={labelType.item} key="labelType"/> : ''}
                                <Row className="mt10">
                                    <Col span={2}></Col>
                                    <Col span={22}>
                                        <Select style={{width: 300}}>
                                            {industryData ? industryData.map((item, idx) => <Option value={item.id}
                                                                                                    key={item.id}>{item.name}</Option>) : ''}
                                        </Select>
                                    </Col>
                                </Row>
                                <Row className="mt10">
                                    <Col span={2}></Col>
                                    <Col span={22}>
                                        <RangePicker showTime/>
                                    </Col>
                                </Row>
                                <Label title={labelStatus.title} item={labelStatus.item} key="labelStatus"/>
                                <Label title={labelSource.title} item={labelSource.item} key="labelSource"/>
                                <div className="policyList-button">
                                    <Button type="primary"></Button>
                                    <Button className="ml15"></Button>
                                </div>
                            </div>
                            <p align="right"><Link to="/addPolicy"><Button type="primary"></Button></Link></p>
                            <Table columns={this.columns} dataSource={this.data} pagination={this.pagination}/>
                        </Col>
                    </Row>
                </div>
                {/*<Footer/>*/}
            </div>
        );
    };
}

export default PolicyList;